  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto/roboto-v27-latin_cyrillic-ext_cyrillic-700.woff2") format("woff2"), url("../fonts/roboto/roboto-v27-latin_cyrillic-ext_cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

:root {
  --content-width: 1300px;
  --gutter: 12px;
  --mob-padding: 20px; }

/*
@mixin offset($size) {
  margin-left: calc(100% * $size - var(--gutter));
}

*/
.container {
  max-width: var(--content-width);
  margin-left: auto;
  margin-right: auto;
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding); }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1); }

:root {
  --bg-body: #000000;
  --color-white: white;
  --color-bg-header: #000000; }

img {
  display: block;
  max-width: 100%; }

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  transition: 0.2s; }
  .header_has-bg {
    background: var(--color-bg-header); }
  @media (max-width: 992px) {
    .header {
      background: red; } }
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px; }
  .header__menu {
    display: flex;
    justify-content: space-between; }
    .header__menu li > a {
      color: var(--color-white);
      font-style: normal;
      font-weight: normal;
      font-size: 16px; }
    .header__menu .sub-menu {
      display: none; }
  .header__cta {
    color: var(--color-white); }

.promo {
  padding-top: 270px;
  padding-bottom: 200px;
  background-image: url(../img/promo/promo_bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }
  .promo__title {
    max-width: 920px;
    color: var(--color-white);
    font-weight: bold;
    font-size: 64px;
    line-height: 140%;
    margin-bottom: 50px; }
  .promo__time {
    display: flex;
    justify-content: space-between;
    max-width: 522px; }
  .promo__time-item {
    background: #121212;
    padding: 20px 30px; }
  .promo__time-number {
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    color: var(--color-white);
    line-height: 140%; }
  .promo__time-word {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    color: var(--color-white);
    opacity: 0.4;
    line-height: 140%; }
